.chart-wrapper {
    margin-top: 0px;
    margin-left: 50%;
    transform: translate(-50%);
    width: 900px;
    padding: 20px 50px;
}
.chart-screen {
    top: 0;
    width: 100vw;
    height: 750px;
    position: absolute;
    background-color: rgb(167, 203, 227);
}
.chart-title {
    margin-left: 50%;
    transform: translate(-50%);
    margin-top: 110px;
    font-weight: 500;
    width: 400px;
    height: auto;
    padding: 10px;
    /* background-color: #fff; */
    color: rgb(100, 100, 100);
    letter-spacing: 2px;
}
.chart-header {
    height: 60px;
}
.icon-wrapper {
    width: 100vw;
    text-align: center;
}
.chart-dropdowns {
    margin: 20px 10px 0 10px;
    font-size: 16px;
    height: 25px;
    width: auto;
    border: none;
    text-align: center;
    cursor: pointer;
}
.chart-dropdowns:focus {
    outline: none;
}
.key-instructions {
    font-size: 12px;
    color: rgb(100, 100, 100);
}
