body{
    background-color: rgb(167, 203, 227)
}
.login-screen-background-image {
    min-height: 770px;
    height: 100vh;
    width: 100vw;
    background-image: url(../img/login.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    z-index: 1;
    background-color: rgb(167, 203, 227);
    overflow: hidden;
}
.login-box {
    position: absolute;
    width: 400px;
    height: 400px;
    background-color: rgba(255, 255, 255, 0.8);
    border-radius: 10px;
    margin-left: 50%;
    margin-top: 150px;
    transform: translate(-50%);
    transition-timing-function: cubic-bezier(0.23, 1, 0.320, 1);
}
.login-box-title {
    width: 100%;
    margin-top: 50px;
    font-size: 40px;
    font-weight: 300;
    text-align: left;
    padding-left: 75px;
}
.login-input-boxes {
    width: 60%;
    height: 30px;
    font-size: 20px;
    margin-top: 20px;
    border-radius: 5px;
    border: none;
    padding: 1.5%;
}
.login-input-boxes:focus {
    outline: none;
}
.login-button {
    width: 63%;
    height: 30px;
    padding-top: 10px;
    background-color: #DD4C44;
    border: 1px solid #fff;
    color: #fff;
    border-radius: 5px;
    margin-left: 18.5%;
    margin-top: 30px;
    cursor: pointer;
    transition-duration: 200ms;
}
.login-button:hover {
    background-color: #fff;
    border: 1px solid #DD4C44;
    color: #DD4C44;
}