.nav-menu {
    position: absolute;
    top: 0;
    z-index: 2;
    width: 100vw;
    height: 80px;
    background-color: #fff;
}
.nav-logo {
    position: absolute;
    margin-top: 18px;
    margin-left: 18%;
    display: block;
    width: 81px;
    height: 41.3px;
    background-image: url(../img/5c9c9a6b202d4367c4131a0b_logotype.svg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}
.nav-link-wrapper {
    margin-left: 12%;
}
.nav-link {
    overflow: visible;
    margin-top: 14px;
    padding-top: 20px;
    color: rgba(0, 0, 0, 0.8);
    font-size: 11px;
    font-weight: 600;
    letter-spacing: 2px;
    text-transform: uppercase;
    cursor: pointer;
}
.nav-link:hover {
    color: rgb(126, 126, 126);
    padding-top: 18px;
}
.w-nav-link {
    position: relative;
    display: inline-block;
    vertical-align: top;
    text-decoration: none;
    color: #222222;
    padding: 20px;
    text-align: left;
    margin-left: auto;
    margin-right: auto;
}