.error-box {
    margin-left: -100vw;
    top: 0;
    position: absolute;
    position: fixed;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 0;
    overflow: hidden;
}
.error-box-content {
    position: absolute;
    width: 400px;
    height: 400px;
    background-color: rgba(255, 255, 255, 1);
    border-radius: 10px;
    margin-left: 50%;
    margin-top: 850px;
    transform: translate(-50%);
    transition-duration: 500ms;
    transition-timing-function: cubic-bezier(0.23, 1, 0.320, 1);
    
}
.error-box-title {
    margin-left: 10%;
    width: 80%;
    margin-top: 10px;
}
.error-box-button {
    width: 63%;
    height: 30px;
    padding-top: 10px;
    background-color: #DD4C44;
    border: 1px solid #fff;
    color: #fff;
    border-radius: 5px;
    margin-left: 18.5%;
    margin-top: 50px;
    cursor: pointer;
    transition-duration: 200ms;
}
.error-shoes {
    width: 150px;
    height: 100px;
    margin-top: 60px;
    margin-left: 48%;
    transform: translate(-50%);
    background-image: url(../img/error-shoes.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
.error-box-button:hover{
    background-color: #fff;
    border: 1px solid #DD4C44;
    color: #DD4C44;
}
.the-error-text {
    color: #000;
}